import { MenuItemProps } from "../components/MenuItem/MenuItem";

export type SEOData = {
  [x: string]: string | undefined;
  title: string;
  description: string;
  h1: string;
  canonicalUrl: string;
  schema: string;
};

export function generateSEO(product: MenuItemProps): SEOData {
  const { title, description, mainCategory, subCategory, pricesBySize, price, isWeight, image } = product;

  //   
  const minPrice = pricesBySize && Object.keys(pricesBySize).length > 0
    ? Math.min(...Object.values(pricesBySize))
    : price ?? 0;

  //    "",   
  const priceText = isWeight ? ` ${minPrice} /` : ` ${minPrice} `;

  //  title  description   
  const seoTitle = `${title} |    ${priceText}  `;
  const seoDescription = `${description} ${mainCategory ? `| ${mainCategory}` : ''} ${subCategory ? `| ${subCategory}` : ''}.  ${title}        . ${priceText}.`;
  //  slug
  const slug = product.urlId || title
    .toLowerCase()
    .replace(/\s+/g, '-')
    .replace(/[^\w\-]/g, '');

  const canonicalUrl = `https://stroydom30.ru/product/${slug}`;

  // Schema.org
  const schema = {
    "@context": "https://schema.org",
    "@type": "Product",
    name: title,
    description: description,
    image: `https://stroydom30.ru${image}`,
    url: canonicalUrl,
    brand: {
      "@type": "Organization",
      name: ""
    },
    offers: {
      "@type": "Offer",
      priceCurrency: "RUB",
      price: minPrice,
      availability: "https://schema.org/InStock",
      areaServed: "RU",
      availableAtOrFrom: {
        "@type": "Place",
        name: "",
        address: {
          "@type": "PostalAddress",
          addressLocality: "",
          addressRegion: " ",
          addressCountry: ""
        }
      }
    }
  };

  return {
    title: seoTitle,
    description: seoDescription,
    h1: title,
    canonicalUrl,
    schema: JSON.stringify(schema),
  };
}

